import Clipboard from "clipboard";
import vueQr from 'vue-qr'
import {hideStrMid} from "../../plugins/utils";

export default {
  layout: "single-container",
  name: "Signup",
  components: {
    vueQr
  },
  data() {
    return {
      address: '',
    };
  },
  computed: {},
  mounted() {
    this.initCopy();
    this.getDepositAddress();
  },
  methods: {
    async getDepositAddress() {
      let data = await this.$axios.post('/v1/udc/GetDepositAddress', {
        sChain: 'Ethereum',
        sToken: 'NERA',
        iChannel: 0,
      })
      this.address = data.sAddress;
    },
    initCopy() {
      let btn = document.getElementById("copy");
      let clipboard = new Clipboard(btn);

      clipboard.on("success", () => {
        // this.$message.success(this.$t('message.lang_62'));
        this.$toast(this.$t('Mine32'));
      });

      clipboard.on("error", function (e) {
      });
    },
    getAddress(str, len) {
      return hideStrMid(str, len)
    },
  },
};
